import React from 'react';
import h from './Header.module.css' ;
import {NavLink} from "react-router-dom";
import logo from "../assets/images/it_logo.jpg";
import nav from "./Navbar.module.css";
import letter from "../assets/images/letter2.png"

const Header = (props) => {
    return (
        <header className={h.header}>
            <div className={h.logoContainer}>
                <NavLink to="/profile">

                <img className={h.logo}
                     // src="https://upload.wikimedia.org/wikipedia/commons/thumb/a/a6/Radio.svg/1200px-Radio.svg.png"
                     src={logo}
                     alt="logo"/>
                </NavLink>

            </div>


            <div className={h.menu}>
                <div className={h.linkContainer}>
                    <NavLink activeClassName={h.activeLink} to="/profile">
                        Profile
                        </NavLink>
                </div>

                <div className={h.linkContainer}>

                    <div style={{display: 'flex'}}>
                        <NavLink activeClassName={h.activeLink} to="/dialogs">
                            <div style={{display: 'flex', alignItems: 'center'}}>
                                <div>Messages</div>
                                {/* new messages*/}
                                {props.newMessageCount ?
                                    <>
                                        <img className={h.msgImg} src={letter} alt="letter"/>
                                        <div className={h.msgCounter}>{props.newMessageCount}</div>
                                    </>
                                    : null
                                }

                            </div>
                        </NavLink>

                    </div>
                </div>

                <div className={h.linkContainer}>
                    <NavLink activeClassName={h.activeLink} to="/news">News</NavLink>
                </div>
               {/* <div className={h.linkContainer}>
                    <NavLink activeClassName={h.activeLink} to="/music">Music</NavLink>
                </div>*/}
                <div className={h.linkContainer}>
                    <NavLink activeClassName={h.activeLink} to="/settings">Settings</NavLink>
                </div>
                <div className={h.linkContainer}>
                    <NavLink activeClassName={h.activeLink} to="/friends">Friends</NavLink>
                </div>
                <div className={h.linkContainer}>
                    <NavLink activeClassName={h.activeLink} to="/users">Users</NavLink>
                </div>


                {/*<a href='#s'>News </a>*/}
                {/*<a href='#s'>Feed</a>*/}
                {/*<a href='#s'>Messages</a>*/}
            </div>
            <div className={h.login}>
                {(props.isAuth)
                    ? (<><div className={h.button} onClick={props.LogoutThunk}>Logout</div></>)
                    :  <NavLink className={h.button} to={'login'}>Login</NavLink>}
            </div>

        </header>
    );
}

export default Header;