import {AuthAPI, SecurityAPI} from "../api/api";
import {stopSubmit} from "redux-form";

const SET_USER_DATA = 'SET_USER_DATA';
const IS_FETCHING = 'IS_FETCHING';
const GET_CAPTCHA_SUCCESS = 'GET_CAPTCHA_SUCCESS';

export const setAuthUserData = (userId, email, login, isAuth= false) => ({type: SET_USER_DATA, payload:{userId, email, login, isAuth}});
export const setIsFetching = (isFetching) =>  ({type: IS_FETCHING, isFetching});
export const getCaptchaSuccess = (captchaUrl) => ({type: GET_CAPTCHA_SUCCESS, captchaUrl});

// get id, email, login if user is auth
export const AuthMeThunk = () =>
     (dispatch) => {
        // dispatch(setIsFetching(true));
        //  try {
             return AuthAPI.AuthMe().then(response => {
                     // dispatch(setIsFetching(false));
                     if(response.data.resultCode === 0) {
                         let {id, email, login} = response.data.data;
                         dispatch(setAuthUserData(id, email, login, true));
                     }
                 }
             );
         // } catch (e) {
         //     console.log(e);
         // }
    };


// log in
export const LoginThunk = (email, password, rememberMe, captcha= null) => {
    return (dispatch) => {
        dispatch(setIsFetching(true));
        AuthAPI.Login(email, password, rememberMe, captcha).then(response => {
            // console.log(response);
            //succes
            if(response.data.resultCode === 0) {
                // console.log(response.data)

                //обнуляем картинку капчи
                dispatch(getCaptchaSuccess(''));
                let {userId} = response.data.data;

                //need login
                dispatch(AuthMeThunk());
            }
            // error email or password or captcha (code=10)
            else {
                //need captcha
                if(response.data.resultCode === 10) {
                    dispatch(GetCaptcha());
                }
                let message = (response.data.messages && response.data.messages.length > 0)
                    ? response.data.messages[0]
                    : "Common error of authorization";
                dispatch(stopSubmit("loginForm", {_error: message }));
            }
        });
        dispatch(setIsFetching(false));
    }
}

export const LogoutThunk = () => {
    return (dispatch) => {
        AuthAPI.Logout().then(res => {
            console.log(res);
            if (res.data.resultCode === 0) {
                let options = {userId: null, email: null, login: null, isAuth: false};
                dispatch(setAuthUserData(options));
            }
        });
    }
}

// get capture
export const GetCaptcha = () => {
    return dispatch => {
        SecurityAPI.getCaptcha().then(res => {
            console.log(res.data.url);
            dispatch(getCaptchaSuccess(res.data.url));
            // console.log(res.data.url);
        });
    }
}

const initialState = {
    userId: null,
    email: null,
    login: null,
    isFetching: false,
    isAuth: false,
    captchaUrl: null,
}

const authReducer = (state = initialState, action) => {

    switch (action.type) {
        case (SET_USER_DATA) :
            return {
                ...state,
                ...action.payload,
                // isAuth: true,
            };
        case (IS_FETCHING) :
            return {
                ...state,
                isFetching: action.isFetching,
            };

        case (GET_CAPTCHA_SUCCESS): {
            return {...state, captchaUrl: action.captchaUrl};
        }

        default:
            return (state);
    }

}

export default authReducer;