import {DialogsApi} from "../api/api";

const ADD_MSG = 'ADD_MSG';
const SET_DIALOGS = 'SET_DIALOGS';
const SET_MESSAGES = 'SET_MESSAGES';
const SET_NEW_MESSAGES_COUNT = 'SET_NEW_MESSAGES_COUNT';

export const addMsgActionCreator = (message) => ({type: ADD_MSG, message});
export const setDialogs = (dialogs) => ({type: SET_DIALOGS, dialogs});
export const setMessages = (messages) => ({type: SET_MESSAGES, messages});
export const setNewMessagesCount = (count) => ({type: SET_NEW_MESSAGES_COUNT, count});

// thunk

export const getAllDialogs = () => {
    /*[{
        hasNewMessages: false
        id: 25643
        lastDialogActivityDate: "2022-10-19T17:28:12.537"
        lastUserActivityDate: "2022-10-19T06:55:25.787"
        newMessagesCount: 0
        photos: Object { small: "https://social-network.samuraijs.com/activecontent/images/users/25643/user-small.jpg?v=1",
         large: "https://social-network.samuraijs.com/activecontent/images/users/25643/user.jpg?v=1" }
        userName: "MashaKustova"}, ......]
    * */
    return dispatch => {
        DialogsApi.GetAllDialogs().then(res => {
            if (res.data && res.data.length > 0) {
                dispatch(setDialogs(res.data));

                res.data.map(dialog=>{
                 dispatch(getMessagesList(dialog.id));
                })
            }
        });
    }
}
// just to top in all dialogs or what? idk
export const getDialogWithFriend = (friendId) => {
    return (dispatch) => {
        DialogsApi.GetDialogWithFriend(friendId).then(res => console.log(res, 'dialogs'));
    }
};

export const getMessagesList = (friendId) => {
    /*
        addedAt: "2022-10-19T17:13:12.883"
        body: "Привет, самурай!"
        id: "fce61907-d97e-4e58-b552-119576e2e4c1"
        recipientId: 25643
        senderId: 18684
        senderName: "JollyRoger"
        translatedBody: null
        viewed: false
    * */
    return (dispatch) => {
        return DialogsApi.getMessagesList(friendId)
            .then(res => {
                console.log(res.items, 'dialogs messsages list');
                if (res.items && res.items.length>0) {
                    dispatch(setMessages(res.items));
                }

            });
    }
};

export const sendMessage = (friendId, message) => {
    return (dispatch) => {
        DialogsApi.sendMessage(friendId, message).then(res => console.log(res, 'send message'));
    }
};

export const countNewMessages = () => {
    return (dispatch) => {
        DialogsApi.getCountOfNewMessages().then(res => {
            console.log(res, 'count new msg')
            if(res.data || true) {
              dispatch(setNewMessagesCount(+res.data));
            }
        });
    }
};


const initialState = {
    dialogs: [
        {
            id: 1, hasNewMessages: false, userName: 'John', lastDialogActivityDate: "2022-10-19T17:28:12.537",
            lastUserActivityDate: "2022-10-19T06:55:25.787", newMessagesCount: 0, photos: {small: null, large: null}
        },
        {
            id: 2, hasNewMessages: false, userName: 'Bob', lastDialogActivityDate: "2022-10-19T17:28:12.537",
            lastUserActivityDate: "2022-10-19T06:55:25.787", newMessagesCount: 0, photos: {small: null, large: null}
        },
        {
            id: 3, hasNewMessages: false, userName: 'Karrie', lastDialogActivityDate: "2022-10-19T17:28:12.537",
            lastUserActivityDate: "2022-10-19T06:55:25.787", newMessagesCount: 0, photos: {small: null, large: null}
        },

    ],

    messages: [
        {   body: 'hi',addedAt: "2022-10-19T17:13:12.883",
            id: "fce61907-d97e-4e58-b552-119576e2e4c1",
            recipientId: 18684,
            senderId: 25643,
            senderName: "JollyRoger",
            translatedBody: null,
            viewed: false},
       /* {id: 2, body: 'shalom'},
        {id: 3, body: 'hek lol keck'},
        {id: 4, body: 'kuku'},*/
    ],
    newMessageCount: null,
}

const dialogsReducer = (state = initialState, action) => {

    switch (action.type) {
        case (ADD_MSG) :
            let newMsg = {id: 15, message: action.message}
            return {
                ...state,
                messages: [...state.messages, newMsg],
            };
        case (SET_DIALOGS): {
            return {...state, dialogs: action.dialogs}
        };
        case (SET_MESSAGES): {
            return {...state, messages: [...state.messages, ...action.messages]}
        };
        case (SET_NEW_MESSAGES_COUNT): {
            return {...state, newMessageCount: action.count}
        }

        default:
            return (state);
    }

}

export default dialogsReducer;