import * as axios from "axios";
// import React from 'react';
// const axios = require('axios').default;


const instance = axios.create({
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    withCredentials: true,
    headers: {
        'API-KEY': '537bb101-34d5-4e34-81ad-544337f8e7e9'
    }
});

export const AuthAPI = {
    AuthMe() {
            return instance.get(`auth/me`);
    },

    Login(email, password, rememberMe = false, captcha= null) {
            return instance.post(`/auth/login`, {email, password, rememberMe, captcha});
            // .then(res=>console.log(res, ' resault of login '));
    },

    Logout() {
            return instance.delete(`auth/login`);
    }

}

export const SecurityAPI = {
    getCaptcha () {
            return instance.get(`security/get-captcha-url`);
    }
}

export const UsersAPI = {

    GetProfileData(id) {
            console.warn('Obsolete method, please use ProfileApi object here');
            return ProfileApi.GetProfileData(id);

    },

    getUsers(selectedPage = 1, pageSize = 10) {
            return instance.get(`users?page=${selectedPage}&count=${pageSize}`)
                .then(response => response.data);
    },

    Follow(userId) {
            return instance.post(`follow/${userId}`, {},)
                .then(response => response.data);
    },

    UnFollow(userId) {
            return instance.delete(`follow/${userId}`)
                .then(response => response.data);
    },
}

export const ProfileApi = {

    GetStatus(userId = 2) {
            return instance.get(`profile/status/${userId}`);
    },

    UpdateStatus(newStatus) {
            return instance.put(`profile/status`, {status: newStatus});
    },

    GetProfileData(id) {
            return instance.get(`profile/${id}`).then(response => response.data);
    },

    UploadAva(file) {
        let formData = new FormData();
        formData.append("image", file);
            return instance.put(`profile/photo`, formData, {headers: {'Content-Type': 'multipart/form-data'}})
                .then(response => response.data);

    },

    SaveProfile(profileData) {
            return instance.put(`profile`, profileData).then(response => response.data);
    }
};


//25643
export const DialogsApi = {
    GetAllDialogs() {
        return instance.get(`dialogs`);
    },

    GetDialogWithFriend(friendId) {
        return instance.put(`dialogs/${friendId}`, friendId).then(res => res.data);
    },

    getMessagesList(friendId) {
        return instance.get(`dialogs/${friendId}/messages`).then(res => res.data);
    },

    sendMessage(friendId, message) {
        return instance.post(`dialogs/${friendId}/messages`, {body: message}).then(res => res.data)
            .catch(e=> console.log(e));
    },

    getCountOfNewMessages() {
        return instance.get(`dialogs/messages/new/count`);
    }
}




