import React from "react";
// import i from "../../Users/Users.module.css";
import load from "../../../assets/images/loader.gif";


const StatusPreloader = () => {
    return (
        <div className={'i.loadCont'} style={{margin: '0 auto'}}  >
            <img className={'i.load'} style={{height: '60px', width: '60px'}} src={load} alt='preload'/>
        </div>
    )
}

export default StatusPreloader;